body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  text-align: left;
  background: #87b5b5;
  margin: 10px 0;
}

textarea {
  height: 100%;
  width: 100%;
}

label {
  font-size: 20px;
  font-weight: bold;
}

#editor-container,
#preview-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 47.5vh;
  width: 75vw;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 10px;
}

#preview {
  padding: 0 1rem;
}

#preview blockquote {
  border-left: 3px solid #224b4b;
  color: #224b4b;
  padding-left: 5px;
  margin-left: 25px;
}

#preview > p > code {
  padding: 3px;
}

#preview code {
  background: white;
  font-size: 0.875rem;
  font-weight: bold;
}

#preview pre {
  display: block;
  overflow: auto;
  background: white;
  padding: 5px;
  line-height: 1.2;
}

#preview h1 {
  border-bottom: 2px solid #224b4b;
}

#preview h2 {
  border-bottom: 1px solid #224b4b;
}

#preview table {
  border-collapse: collapse;
}

#preview td,
#preview th {
  border: 2px solid #224b4b;
  padding-left: 5px;
  padding-right: 5px;
}

#preview img {
  display: block;
  max-width: 90%;
  margin: 2rem auto;
}

.box {
  border: 2px solid;
}

@media screen and (max-width: 420px) {
  body {
    font-size: 0.875rem;
  }
}

